<template>
  <div class="content-info" >
    <div style="margin-bottom: 20px;height: auto;position: relative">
      <a-image v-if="['img','url'].indexOf(focusedItem.type)>-1"
        :width="200" style="max-height: 300px"
        :src="cover"
      >
        <template #placeholder>
          <a-image
            src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png?x-oss-process=image/blur,r_50,s_50/quality,q_1/resize,m_mfit,h_200,w_200"
            :width="200"
            :preview="false"
          />
        </template>
      </a-image>
      <a-image v-else :style="{objectFit:'cover',padding:20+'px'}" :width="200" :height="190"
               :preview="false" style="background: white;"
               class="sub-folder-cover"
               :src="'./img/type/other/'+focusedItem.type+'.svg'">
      </a-image>
      <img v-if="focusedItem.extData.favicon" onerror="this.src='./img/default.svg'" :src="focusedItem.extData.favicon" class="favicon" style="bottom: -10px;right: -10px">
    </div>
    <div style="margin-bottom: 10px">
      <a-textarea @blur="rename()" v-model:value="userInput.name" :autSize="{ minRows: 1, maxRows: 5 }"  style="margin-bottom: 10px" placeholder="标题"/>
<!--      <a-select-->
<!--        :options="options"-->
<!--        mode="tags"-->
<!--        placeholder="选择标签"-->
<!--        style="width: 100%"-->
<!--      ></a-select>-->
      <a-textarea @blur="editRemark()" style="margin-bottom: 10px" v-model:value="userInput.remark" placeholder="备注"></a-textarea>
      <a-input @blur="editHref()" style="margin-bottom: 0" v-model:value="userInput.href" placeholder="网址网址">
        <template #suffix>
          <ExportOutlined title="打开引用页" @click="ipc.addTab(focusedItem.meta.href)"/>
        </template>
      </a-input>
      <a-rate @change="setRate" v-model:value="userInput.rate" style="margin-bottom: 10px" :value="4" allow-half/>
<!--      <a-select-->
<!--        :options="options"-->
<!--        mode="tags"-->
<!--        placeholder="标记为"-->
<!--        style="width: 100%"-->
<!--      ></a-select>-->
    </div>
    <div style="margin-bottom: 10px">
      <h4>基本信息</h4>
      <div>
        创建时间：{{ this.dateStr(focusedItem.baseInfo.createTime) }}
        <br>
        添加时间：{{ this.dateStr(focusedItem.baseInfo.addTime) }}
        <br>
        修改时间：{{ this.dateStr(focusedItem.baseInfo.modifyTime) }}
        <br>
        文件路径：<br><span title="点击定位到文件" @click="ipc.showItemInFolder(focusedItem.baseInfo.path+focusedItem.baseInfo.filename)" style="color: #999;cursor:pointer;">{{ focusedItem.baseInfo.relativePath }}{{focusedItem.baseInfo.filename}}</span>
        <br/>
        文件大小：{{focusedItem.baseInfo.sizeText}}
      </div>
    </div>
    <div>
      <a-button @click="exportContent(focusedItem)" block>导出文件</a-button>
    </div>
  </div>

</template>

<script>
import { ref, defineComponent } from 'vue'
import {ExportOutlined} from '@ant-design/icons-vue'
import { mapState } from 'vuex'
import ipc from '@/utils/ipc'
import contentModel from '@/utils/model/contentModel'
import {dateStr} from '@/utils/time'
import statusReturn from '@/utils/statusReturn'
import fileModel from '@/utils/model/fileModel'
import pathTools from '@/utils/pathTools'
export default defineComponent({
  name: 'FocusItemContent',
  props: {},
  components:{
    ExportOutlined
  },
  data () {
    return {
      ipc,
      value3: ref(['a1', 'b2']),
      options: [
        { value: '稍后读' },
        { value: '代办' },
        { value: '待整理' },
        { value: '待标注' },
      ],
      userInput:{
        name:'',//文件名
        remark:'',//备注
        rate:0,//星
        href:'',//引用
      }
    }
  },
  mounted () {
    this.userInput.name=this.focusedItem.meta.name
    this.userInput.remark=this.focusedItem.meta.remark || ''
    this.userInput.rate=this.focusedItem.meta.rate || 0
    this.userInput.href=this.focusedItem.meta.href || ''
  },
  methods: {
    dateStr,
    exportContent(content){
      let path=pathTools.getStorePathFromRelativePath(content.meta.relative_path_full)
      fileModel.saveAs(path)
    },
    /**
     * 重命名内容
     */
    rename(){
      if(this.userInput.name===this.focusedItem.name || this.userInput.name===''){
        this.userInput.name=this.focusedItem.name
        return
      }
      let renameResult= contentModel.rename(this.focusedItem.meta.uuid,this.userInput.name)
      if(renameResult.status===statusReturn.successCode){
        this.$message.success('修改名称成功。')
        this.$store.commit('content/updateContentWithUuid',
            {uuid:this.focusedItem.meta.uuid,
          meta:renameResult.data})
      }else{
        this.userInput.name=this.focusedItem.meta.name
        this.$message.error('修改名称失败，原因：'+renameResult.info)
      }
    },
    editRemark(){
      if(this.userInput.remark===this.focusedItem.meta.remark){
        return
      }
      let remarkResult= contentModel.update(this.focusedItem.meta.uuid, { remark:this.userInput.remark })
      if(remarkResult.status===statusReturn.successCode){
        //this.$message.success('修改备注成功。')
        this.$store.commit('content/updateContentWithUuid',
            {uuid:this.focusedItem.meta.uuid,
              meta:remarkResult.data})
      }else{
        this.userInput.remark=this.focusedItem.meta.remark
        this.$message.error('修改备注失败，原因：'+remarkResult.info)
      }
    },
    editHref(){
      if(this.userInput.href===this.focusedItem.meta.href){
        return
      }
      let result= contentModel.update(this.focusedItem.meta.uuid, { href:this.userInput.href })
      if(result.status===statusReturn.successCode){
        //this.$message.success('修改备注成功。')
        this.$store.commit('content/updateContentWithUuid',
            {uuid:this.focusedItem.meta.uuid,
              meta:result.data})
      }else{
        this.userInput.href=this.focusedItem.meta.href
        this.$message.error('修改链接失败，原因：'+result.info)
      }
    },
    setRate(){
     let rateResult= contentModel.update(this.focusedItem.meta.uuid, { rate:this.userInput.rate })
      this.$store.commit('content/updateContentWithUuid',
          {uuid:this.focusedItem.meta.uuid,
            meta:rateResult.data})
    }
  },
  watch:{
    'focusedItem.meta.name':{
      handler(newValue,oldValue){
        this.userInput.name=newValue
        oldValue
      }
    },
    'focusedItem.meta.remark':{
      handler(newValue,oldValue){
        this.userInput.remark=newValue
        oldValue
      }
    },
    'focusedItem.meta.href':{
      handler(newValue,oldValue){
        this.userInput.href=newValue
        oldValue
      }
    },
    'focusedItem.meta.rate':{
      handler(newValue,oldValue){
        this.userInput.rate=newValue || 0
        oldValue
      }
    }
  },
  computed: {
    ...mapState(
      {
        focusedItem: state => state.ui.focusedItem.data
      }),
    cover(){
      if(this.focusedItem.cover){
        if(this.focusedItem.cover.startsWith('/')){
          return 'file://'+this.$store.state.config.storePath+this.focusedItem.cover
        }else{
          return this.focusedItem.cover
        }
      }else{
        return ''
      }
    }
  },
  setup () {
    return {
      random: ref(),
    }
  }
})
</script>

